.dashboardFrame{
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100vh;
    margin: 40px 0;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
.dashboardFrame::-webkit-scrollbar{
    display: none; /* chrome */
}

.profileSection{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.profilePic{
    background-position: center;
    width: 40vw;
    height: 40vw;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
}
.profileContents{
    display: flex;
    flex-direction: column;
    line-height: 0;
    /* background-color: #00000038;
    padding: 15px;
    border-radius: 10px; */
}
.userName{
    font-size: 20px;
    text-transform: capitalize;
}
.userData{
    font-size: 15px;
}

.linksSection{
    border-top-left-radius: 180px;
    border-top-right-radius: 180px;
    background-color: #eab473;
    height: 100%;
    margin: -41px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.linksContainer{
    display: flex;
    padding: 71px;
    flex-direction: column;
    gap: 15px;
}
.linkCard{
    display: flex;
    flex-direction: column;
    background-color: #00000085;
    border-radius: 50px;
    padding: 0px 50px;
    width: 200px;
}
.linkName{
    line-height: 0;
}
a{
    color: #2eaeb7;
    text-decoration: none;
}
.addNewIcon{
    width: 40px;
}